@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@600;700&display=swap);
.mobile-header{background-color:#fff;box-shadow:0px 0px 31px -21px rgba(43,46,49,0.7);border-radius:0 0 25px 25px;text-align:center;padding:15px}@media screen and (min-width: 767px){.mobile-header{display:none}}

.navbar-desktop_block{display:none;padding:40px}.navbar-desktop_block .navbar-desktop_flex{display:flex;width:100%;justify-content:space-between}.navbar-desktop_block .navbar-desktop_flex .logo{position:relative;display:flex;align-items:start;text-decoration:none;color:#fc1055;fill:#fc1055}.navbar-desktop_block .navbar-desktop_flex .logo .logo-icon{position:absolute;top:-30%;left:2%}.navbar-desktop_block .navbar-desktop_flex .logo .E{margin-right:35%}.navbar-desktop_block .navbar-desktop ul{display:grid;grid-template-columns:repeat(4, minmax(100px, 1fr))}.navbar-desktop_block .navbar-desktop ul li{margin-right:10px}.navbar-desktop_block .navbar-desktop ul li:last-child{margin-right:0}.navbar-desktop_block .navbar-desktop ul li a{text-decoration:none;color:#373a42;transition:0.3s ease-in}.navbar-desktop_block .navbar-desktop ul li:hover a,.navbar-desktop_block .navbar-desktop ul li:active a,.navbar-desktop_block .navbar-desktop ul li:focus a{color:#fc1055}@media screen and (min-width: 767px){.navbar-desktop_block{display:block}}

.navbar-mobile{padding-left:40px;padding-right:40px;background-color:#fff;height:90px;width:100%;position:absolute;bottom:0;left:0;box-sizing:border-box;z-index:99}.navbar-mobile ul{display:flex;flex-direction:row;justify-content:space-between;height:100%;align-items:center}.navbar-mobile ul li{list-style:none}.navbar-mobile ul a .navbar_icon{transition:all 0.3s ease;fill:#c2c6ce}.navbar-mobile ul a .navbar_icon:hover{fill:#c77f7f}.navbar-mobile ul a .navbar_icon:focus-visible,.navbar-mobile ul a .navbar_icon:active{fill:#fc1055}@media screen and (min-width: 767px){.navbar-mobile{display:none}}

.short-info-card{max-width:350px;min-width:300px;height:160px;border-radius:20px;background-position:center;background-size:cover;padding:13px 16px;position:relative;overflow:hidden;box-shadow:0px 0px 31px -21px rgba(43,46,49,0.4)}.short-info-card .overlay{background:linear-gradient(360deg, rgba(0,0,0,0.647794) 0%, rgba(0,0,0,0) 78%, rgba(0,212,255,0) 100%);width:100%;height:100%;position:absolute;top:0;left:0}.short-info-card .name{color:#fff;font-size:22px;margin-bottom:8px}.short-info-card .genre{color:#bdc0c4;font-size:12px}.short-info-card .genre .note-icon{fill:#bdc0c4}.short-info-card .genre-label{margin-left:5px}.short-info-card .link{color:#fff;font-size:15px;text-decoration:none;position:absolute;bottom:13px}

.wrapper-collections-slick-slider{margin:20px auto;width:95%;position:relative}.wrapper-collections-slick-slider .collections-slick-slider .slick-slide{margin-right:10px}.wrapper-collections-slick-slider .left-overlay{position:absolute;height:190px;width:70px;left:0;top:0;background:linear-gradient(90deg, #f2f2f2 0%, rgba(242,242,242,0) 100%);z-index:2}.wrapper-collections-slick-slider .right-overlay{position:absolute;height:190px;width:70px;right:0;top:0;background:linear-gradient(-90deg, #f2f2f2 0%, rgba(242,242,242,0) 100%);z-index:2}

.detailed-card-component-wrapper{max-width:500px;min-width:300px;height:256px;position:relative;border-radius:20px;overflow:hidden;box-shadow:0px 0px 31px -21px rgba(43,46,49,0.4);transition:0.3s ease all;cursor:pointer}.detailed-card-component-wrapper:hover{box-shadow:0px 0px 31px -21px rgba(43,46,49,0.7)}.detailed-card-component-wrapper .detailed-card-component{display:flex;align-items:flex-end;box-sizing:border-box;width:100%;height:100%;background-size:cover;color:#ffffff;font-weight:600;font-size:12px}.detailed-card-component-wrapper .detailed-card-component .overlay{background:linear-gradient(360deg, rgba(0,0,0,0.647794) 0%, rgba(0,0,0,0) 90%, rgba(0,212,255,0) 100%);width:100%;padding:16px;transition:0.3s ease all}.detailed-card-component-wrapper .detailed-card-component .overlay:hover{background:#000}.detailed-card-component-wrapper .detailed-card-component .overlay .close-btn{opacity:0;width:30px;height:30px;border-radius:50px;background-color:#98989880;border:none;position:absolute;right:5px;top:5px;transition:all 0.3 ease-in;cursor:pointer}.detailed-card-component-wrapper .detailed-card-component .overlay .close-btn:hover{opacity:1}.detailed-card-component-wrapper .detailed-card-component .overlay .close-btn .close-icon{fill:#ffffff}.detailed-card-component-wrapper .detailed-card-component .overlay .info-block{min-height:100px;width:100%}.detailed-card-component-wrapper .detailed-card-component .overlay .info-block .data{margin-bottom:5px;letter-spacing:0.5px;line-height:16px}.detailed-card-component-wrapper .detailed-card-component .overlay .info-block .name{font-size:22px;line-height:24px;margin-bottom:10px}.detailed-card-component-wrapper .detailed-card-component .overlay .info-block .bottom-block{display:flex;justify-content:start;color:#ececec;align-items:center}.detailed-card-component-wrapper .detailed-card-component .overlay .info-block .bottom-block .note-icon{fill:#bdc0c4;width:10px;height:10px;margin-right:7px}.detailed-card-component-wrapper .detailed-card-component .overlay .info-block .bottom-block .genre{margin-right:7px;letter-spacing:1px}.detailed-card-component-wrapper .detailed-card-component .overlay .info-block .bottom-block .tickets-icon{fill:#bdc0c4;width:11px;height:12px;margin-right:7px}.detailed-card-component-wrapper .detailed-card-component .overlay .info-block .bottom-block .price .uah-icon{fill:#bdc0c4;width:8px;height:8px;margin-right:2px}

.container{max-width:1200px;margin:0 auto;padding:16px}.container .title-block{display:flex;justify-content:space-between;margin-bottom:30px;font-weight:700;font-size:28px;align-items:center}.container .title-block .settings-btn{border:none;border-radius:50%;width:32px;height:32px;background:#ffffff;cursor:pointer;display:flex;justify-content:center;align-items:center}.container .title-block .settings-btn .settings-icon{fill:#fc1055}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after{content:'';content:none}q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

